@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{color:#333;font-family:'PT Sans', 'Arial', 'sans-serif'}

.wrapper{align-items:center;display:flex;flex-direction:column;margin:auto;max-width:1200px;padding:0 10px 90px 10px}.start__text{text-align:center}.start__form{display:flex;flex-direction:column;width:280px}

.wrapper{align-items:center;display:flex;flex-direction:column;margin:auto;max-width:1200px;padding:0 10px 90px 10px}.credit-form{display:flex;flex-direction:column;width:100%}.credit-form__wrapper{display:flex;justify-content:space-between}.credit-form__fields{display:flex;flex-direction:column;width:49%}.credit-form__fields>div{margin-top:20px}.credit-form__checkboxes{display:flex;flex-direction:column}.credit-form__file{align-items:center;display:flex;justify-content:center}.credit-form__file-input{display:none}.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline{border-color:#01935d !important}.MuiFormLabel-root.Mui-focused{color:#01935d !important}.MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline{border-color:#01935d !important}.MuiTypography-displayBlock{cursor:pointer}.MuiSvgIcon-root{cursor:pointer}

.header__top{background:linear-gradient(90deg, #007941 0%, #00ab7b 100%);height:35px}.header__bottom{align-items:center;border-bottom:1px solid #ebebeb;display:flex;padding:25px 35px}.header__logo{margin-right:40px}.header__text{border-left:1px solid #333;font-size:18px;padding-left:15px}@media (max-width: 580px){.header__text{display:none}.header__bottom{justify-content:center}.header__logo{margin-right:0px}}@media (max-width: 414px){.header__logo{height:30px}}

.footer{background:white;bottom:0;box-sizing:border-box;position:fixed;width:100%}.footer__delim{background:linear-gradient(90deg, #007941 0%, #00ab7b 100%);height:4px}.footer__main{align-items:center;display:flex;justify-content:space-between;padding:25px 15px}.footer__logo{height:30px}.footer__copyright{font-size:14px}@media (max-width: 414px){.footer__copyright{font-size:12px}.footer__logo{height:25px}}

